import React from "react";

function Results(props) {
    return (
        <div className="container-fluid">
            <table className="table">
                <tbody>
                    <tr>
                        <td> Tom </td>
                        <td> {props.name} </td>
                        <td> {props.cell} </td>
                        <td> {props.email} </td>
                        <td> {props.dob} </td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}

export default Results;